.footer {
  padding: 41px 0 42px;
  background-color: $color-black-russian;

  @include vp-1199 {
    padding: 44px 0;
  }

  @include vp-767 {
    padding: 46px 0 52px;
  }
}

.footer__wrapper {
  display: flex;
  justify-content: space-between;

  @include vp-767 {
    flex-direction: column;
    justify-content: flex-start;
  }
}

.footer__navigation {
  flex-grow: 1;

  @include vp-767 {
    margin-bottom: 17px;
  }
}

.footer__navigation-list {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 40px;
  margin: 0;
  padding: 0 0 0 128px;
  list-style: none;
  color: $color-default-white;

  @include vp-1199 {
    gap: 30px;
  }

  @include vp-767 {
    display: block;
    column-count: 2;
    padding: 0;
  }
}

.footer__navigation-item {
  padding-top: 1px;

  @include vp-1199 {
    padding-top: 0;
  }

  @include vp-767 {
    margin-bottom: 16px;
  }

  &--logo {
    position: absolute;
    top: 2px;
    left: 0;
    padding: 0;

    @include vp-767 {
      position: relative;
      padding: 2px 0 3px;
    }
  }
}

.footer__navigation-link {
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: $color-default-white;

  @include vp-767 {
    font-size: 16px;
    line-height: 19px;
  }

  &--logo {
    display: block;
    line-height: 0;

    @include vp-1199 {
      svg {
        width: 81px;
        height: 18px;
      }
    }

    @include vp-767 {
      svg {
        width: 67px;
        height: 15px;
      }
    }
  }

  @include hover-focus {
    opacity: 0.8;
  }
}

.footer__social {
  min-width: 128px;
}
